<script>
import UploadService from "../services/upload-files.service";
export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data: () => {
    return {
      file: undefined,
      chunkSize: 4 * 1024 * 1024,
      currentChunkStartByte: 0,
      currentChunkFinalByte: undefined,
      videoURL:
        "https://de3c7r60bw5ig.cloudfront.net/courses/60f98d44369bf1178b260152/60f98d44369bf1178b260152/1vgXO1CPmNidXqTOELXduEuRhVX/1vgXO1CPmNidXqTOELXduEuRhVX.m3u8",
    };
  },
  mounted() {
    if (window.Hls.isSupported()) {
      var video = document.getElementById("video");
      var config = {
        xhrSetup: function(xhr, url) {
          console.log(url)
          xhr.withCredentials = true; // do send cookies
        },
      };
      var hls = new window.Hls(config);
      // bind them together
      hls.attachMedia(video);
      hls.on(window.Hls.Events.MEDIA_ATTACHED, function() {
        console.log("video and hls.js are now bound together !");
        hls.loadSource(this.videoURL);
        hls.on(window.Hls.Events.MANIFEST_PARSED, function(event, data) {
          console.log(
            "manifest loaded, found " + data.levels.length + " quality level"
          );
        });
      });
    }
  },
  methods: {
    selectFile() {
      this.file = this.$refs.file.files.item(0);
      this.currentChunkFinalByte =
        this.chunkSize > this.file.size ? this.file.size : this.chunkSize;
      console.log(this.file);
    },
    getCookies() {
      UploadService.getCookies()
        .then((response) => {
          console.log(response);
        })
        .catch((err) => {
          this.file = undefined;
          console.log(err);
        });
    },
    refrehToken() {
      UploadService.refreshToken()
        .then((rep) => {
          console.log(rep);
          sessionStorage.setItem("token", rep.data.accessToken);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    uploadFile() {
      const contentRange = `bytes ${this.currentChunkStartByte}-${this.currentChunkFinalByte}/${this.file.size}`;
      let chunk = this.file.slice(
        this.currentChunkStartByte,
        this.currentChunkFinalByte,
        this.file.type
      );
      console.log(contentRange);

      UploadService.upload(chunk, this.file.name, contentRange, (event) => {
        console.log(event);
      })
        .then((response) => {
          console.log(response);
          const remainingBytes = this.file.size - this.currentChunkFinalByte;

          if (this.currentChunkFinalByte === this.file.size) {
            alert("Yay, download completed! Chao!");
            this.file = undefined;
            return;
          } else if (remainingBytes < this.chunkSize) {
            this.currentChunkStartByte = this.currentChunkFinalByte;
            this.currentChunkFinalByte =
              this.currentChunkStartByte + remainingBytes;
          } else {
            this.currentChunkStartByte = this.currentChunkFinalByte;
            this.currentChunkFinalByte =
              this.currentChunkStartByte + this.chunkSize;
          }

          return this.uploadFile();
        })
        .catch((err) => {
          this.file = undefined;
          console.log(err);
        });
    },
  },
};
</script>

