import http from "../http-common";

class UploadFilesService {
  upload(file, filename, contentRange, onUploadProgress) {
    let formData = new FormData();

    formData.append("video", file, filename);

    return http.post(
      "/sections/60f98d44369bf1178b260152/content/1vgXO1CPmNidXqTOELXduEuRhVX/video",
      formData,
      {
        headers: {
          "Content-Type": "multipart/form-data",
          "Content-Range": contentRange,
        },
        onUploadProgress,
      }
    );
  }

  getCookies() {
    return http.get(
      "/sections/60f98d44369bf1178b260152/content/1vgXO1CPmNidXqTOELXduEuRhVX",
      {
        headers: {
          "Content-Type": "application/json",
        },
        withCredentials: true,
      }
    );
  }

  refreshToken() {
    return http.post("/auth/refresh", {
      refreshToken: "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJyZWZyZXNoX2lkIjoiNjBmZDcxNTRlMDc5OTI4M2NmZWE3NjNmKys2MGRjY2U4NTc4YWU1MmY1NmJjMjMzN2MiLCJ1c2VyX2lkIjoiNjBkY2NlODU3OGFlNTJmNTZiYzIzMzdjIiwiZXhwaXJlc19hdCI6MTYyNzgyNzE1NiwiaXNzIjoidGVzdCJ9.SxdODvFFUbfDu5C2HKLrqTexJpOPekmAlOcrw0i7sf4"
    })
  }
}

export default new UploadFilesService();
